<template>
  <div style="padding:0px 16px;">
    <el-card class="box-card card" v-for="(item,index) in subitem">
      <label for="" class="item" @click="hideOther(index)" v-if="!item.isShowSelect">{{item.text}}<input type="text" v-model="item.value" disabled ></label>
      <myPicker class="picker" :item.sync="item" v-model="item.value" v-else></myPicker>
  </el-card>
  <el-input type="textarea" ></el-input>
  <mt-button class="submit" @click.native="submit">保存</mt-button>
  </div>

</template>

<script>
import myPicker from "./picker.vue"

export default {
  components:{myPicker},
  data() {

    return {
      subitem:null

    };
  },

  methods: {
    hideOther (index){
      this.subitem.map(nav =>{
        nav.isShowSelect=false
      });
      this.subitem[index].isShowSelect=true;
    },
    submit (){
      var resource = this.$resource(this.$store.state.serverIP +"/health_items/add_item_record");
      const health_item_id = this.$route.params.item.id;
      var list=[];
      this.subitem.map(nav =>{
        list.push(nav.value||"")
      });
      const content = list.join(",")
      resource.save({health_item_id,content}).then((callback) => {
        this.$router.push({path:"/index"});
      });
    },
    getArray (min,max){
      var arr=[];
      for (var i =min;i<=max;i++){
        arr,push(i)
      }
      return arr
    }

  },
  computed () {
    var item = JSON.parse(this.$route.params.item);
    if(item.subitem.lenght<=0){
      item.slots=[{
        flex: 1,
        values:this.getArray(item,min,item.max),
        className: 'slot1',
        textAlign: 'center'
      }]
    }else{
      item.subitem.map(nav=>{
        nav.slots=[{
          flex: 1,
          values:this.getArray(nav.min,nav.max),
          className: 'slot1',
          textAlign: 'center'
        }]
      })
    }


  }

}
</script>

<style  scoped>
.card{
  margin: 20px auto;
  text-align: center;
}
.card .picker{
  margin: 0px auto;
}

.card .item input{
    text-align: center;
    margin: 40px auto;
    border: 1px solid #ccc;
    width: 50px;
    font-size: 24px;
    height: 30px;
    line-height: 30px;
    margin-left: 10px;
    border-radius: 4px;
    outline: none;

}
.submit{
	background-color: #A21A28;
	outline: none;
	color: #fff;
	width: 100%;
  margin: 20px auto;
	font-family: '微软雅黑';
}
</style>



